@import '../../variables.scss';

.heading{
    font-size: 30px;
    padding: 15px;
}

.skillsContainer{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin: 60px;
    color: $colour-primary-grey;
}

.skillsListContainer {
    display: flex;
    justify-content: space-evenly;
    list-style-type: none;
    flex-wrap: wrap;
    // width: 60vw;
};

.skills {
    display: flex;
    flex-direction: column;
    padding: 2rem;
    @media screen and (max-width: 550px) {
        padding: 1rem;
    }
    // @media screen and (max-width: 550px) {
    //     padding: 15px;
    // }
};

.icons {
    font-weight: 800;
    font-size: 50px;
    color: $colour-primary-pink;
}


.description{
    padding: 10px;
}